<template>
  <page id="domtest" class="hc-main-home">
    <!-- 头部 -->
    <div class="hc-banner-bar hc-pa hc-f14" slot="header" :style="bannerBarStyle">
      <div @click="showCitySearch" class="hc-banner-loc hc-pa hc-one-in hc-one-in-1">
        {{$store.state.common.location.allName}}
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-lujing11"></use>
        </svg>
      </div>
      <div class="hc-banner-search hc-pr">
        <input placeholder="请输入搜索内容" class="mint-field-core" type="text">
        <div class="hc-pa hc-f16">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-ziyuan8"></use>
          </svg>
        </div>
      </div>
    </div>

    <div  ref="viewBox" slot="content">
      <div class="hc-banner hc-pr">
        <mt-swipe :auto="4000">
          <mt-swipe-item>
            <img class="hc-w100-per hc-h100-per" src="../../assets/img/mall/mall_banner.jpg" alt="">
          </mt-swipe-item>
          <mt-swipe-item>
            <img class="hc-w100-per hc-h100-per" src="../../assets/img/mall/mall_banner.jpg" alt="">
          </mt-swipe-item>
        </mt-swipe>
        <!-- <div class="hc-banner-bar hc-pa hc-f14" :style="bannerBarStyle">
          <div @click="showCitySearch" class="hc-banner-loc hc-pa hc-one-in hc-one-in-1">
            {{$store.state.common.location.allName}}
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-lujing11"></use>
            </svg>
          </div>
          <div class="hc-banner-search hc-pr">
            <input placeholder="请输入搜索内容" class="mint-field-core" type="text">
            <div class="hc-pa hc-f16">
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-ziyuan8"></use>
              </svg>
            </div>
          </div>
        </div> -->
      </div>
      <div class="hc-nav-content hc-f14">
        <mt-swipe :auto="0" :show-indicators="false">
          <mt-swipe-item>
            <div class="hc-nav-item hc-text-center" v-for="(item, index) in listAll" :key="index">
              <div class="hc-w100-per">
                <img :src="item.imgUrl" alt="">
                <div class="hc-one-in hc-one-in-1">{{item.name}}</div>
              </div>
            </div>
          </mt-swipe-item>
        </mt-swipe>
      </div>
      <div class="hc-main-content" v-infinite-scroll="loadMore" infinite-scroll-disabled="moreLoading" infinite-scroll-distance="0" infinite-scroll-immediate-check="'false'">
        <div @click="toStoreDetails" v-for="(item, index) in storeList" :key="index" class="hc-main-item hc-pr hc-clearfix">
          <div class="hc-main-sign hc-pa">
            <img :src="item.imgUrl" alt="">
          </div>
          <div class="hc-main-introduce hc-fl">
            <div class="hc-main-title hc-f16 hc-one-in hc-one-in-1 hc-c333">{{item.storeName}}</div>
            <div class="hc-main-star">
              <star :score='7'/>
            </div>
            <div class="hc-main-address hc-cb3 hc-f12">{{item.adress}}</div>
            <div class="hc-main-location hc-cb3">
              <span>
                <svg class="icon" aria-hidden="true">
                  <use xlink:href="#icon-lujing6"></use>
                </svg>
              </span>
              <span>距离我<i class="hc-cff7">{{item.distance}}km</i></span>
            </div>
            <div class="hc-main-label">
              <div class="hc-main-label-1">
                <div class="hc-main-label-icon hc-cfff hc-inlineblock">
                  <svg class="icon hc-f14" aria-hidden="true">
                    <use xlink:href="#icon-ziyuan1"></use>
                  </svg>
                </div>
                {{item.minimumDelivery}}箱起送
              </div>
              <div class="hc-main-label-1 hc-label2-2">
                支持自取
              </div>
            </div>
          </div>
        </div>
        <div class="hc-text-center hc-loading" v-show="!queryLoading">
          <mt-spinner type="fading-circle" color="#00ccff" :size="20" v-show="moreLoading&&!allLoaded"></mt-spinner>
          <span v-show="allLoaded">已全部加载</span>
        </div>
      </div>

    </div>
  </page>
</template>
<script type="text/babel">
import Star from '../../components/common/star/Star'
import { getCategoryList, createSignature, getStoreBylatAndIng } from '../../api/axios'
import { MessageBox } from 'mint-ui'
import { HcMessageBox } from '../../components/common/hcMessage/index.js'
import wx from 'weixin-js-sdk'
export default {
  name: "MainCom",
  components:{
    Star
  },
  props:{
    scrollTop: String
  },
  data() {
    return {
      // 分类列表
      listAll: [],
      //初始化无限加载相关参数
      queryLoading: false,
      moreLoading: false, // 若为真，则无限滚动不会被触发
      allLoaded: false,
      totalNum: 0,
      pageSize: 10,
      pageNum: 1,
      list: [1, 2, 3, 4, 5, 5, 3, 4, 4, 5],
      value5: 4.5,
      storeList: [] // 附近门店列表
    };
  },
  computed: {
    bannerBarStyle(){
      let tempAph = this.$store.getters.scrollTop;
      let tempColor = '#fff';
      if(tempAph > 0.5){
          tempColor = '#333';
      }else {
        tempColor = '#fff';
      }
      return {
        backgroundColor: `rgba(255,255,255, ${tempAph})`,
        borderBottom: `1px solid rgba(219,219,219, ${tempAph})`,
        color: tempColor
      }
    }
  },

  methods: {
    // 加载更多
    loadMore(){
      if(this.allLoaded){
          this.moreLoading = true;
          return;
      }
      if(this.queryLoading){
          return;
      }
      this.pageNum++;

      this.getStoreBylatAndIng().then(() => {
          this.moreLoading = false;
        if(this.pageNum >= 5){
          this.moreLoading = true;
          this.allLoaded = true;
        }
      });
    },
    // 跳转门店详情页
    toStoreDetails() {
      this.$router.push({path: 'mdcOrder/store'})
    },
    // 获取商品分类
    getCategoryList(){
      getCategoryList().then((result) => {
        this.listAll = result.list;
      })
    },
    // 显示地址搜索框
    showCitySearch() {
      this.$store.commit("POP_STATUS_S", true);
    },
    // 获取微信分参数
    getConfig() {
      let url = location.href.split('#')[0];
      // console.log(url);
      createSignature(url).then((result) => {
        this.wxInit(result)
      })
    },
    // 微信初始化
    wxInit(result){
      wx.config({
        debug: false,
        appId: result.appId,
        timestamp: result.timestamp,
        nonceStr: result.nonceStr,
        signature: result.signature,
        jsApiList: ['checkJsApi', 'openLocation', 'getLocation'] // 所有要调用的 API 都要加到这个列表中
      });
      wx.ready(() => {
        // 检查当前客户端是否支持获取地理位置
        wx.checkJsApi({
          jsApiList: [
            'getLocation'
          ],
          success: function (res) {
            if (res.checkResult.getLocation == false) {
              MessageBox('提示', '你的微信版本太低，不支持微信JS接口，请升级到最新的微信版本！');
              return;
            }
            // 获取经纬度
            wx.getLocation({
              success: function (res) {
                let latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
                let longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
                let accuracy = res.accuracy; // 位置精度
                // 根据经纬度获取附近门店
              },
              cancel: function (res) {
                MessageBox('提示', '用户拒绝授权获取地理位置');
              }
            });
          },
          fail: function(res){
            alert(res)
          }
        });
      });
      wx.error((res) => {

      })
    },
    // 根据经纬度获取附近门店
    getStoreBylatAndIng(){
      return getStoreBylatAndIng('2121','21213213').then((result) => {
        console.log(result.result.list,99999)
        if(result.result.pages <= this.pageNum){
          this.moreLoading = true;
          this.queryLoading = true;
        }
        this.storeList =this.storeList.concat(result.result.list);
        return Promise.resolve();
      })
    }
  },
  mounted(){
    this.$nextTick(function () {
      this.getCategoryList()
      this.getConfig()
      this.getStoreBylatAndIng()

      // 定位测试
      // var citySearch = new AMap.CitySearch()
      // citySearch.getLocalCity((status, data) => {
      //   if (status === 'complete' && data.info === 'OK') {
      //     console.log(data)
      //     this.$store.commit('LOCATION',{
      //         nodeName: data.province,
      //         nodeCode: '',
      //         allName: data.province + data.city
      //     })
      //   }
      // });

      // var geocoder = new AMap.Geocoder({
      //   // city 指定进行编码查询的城市，支持传入城市名、adcode 和 citycode
      //   city: '010'
      // })

      // var lnglat = [113.722, 22.84858]
      // geocoder.getAddress(lnglat, (status, data) => {
      //   if (status === 'complete' && data.info === 'OK') {
      //     // data为对应的地理位置详细信息
      //     data = data.regeocode.addressComponent;
      //     this.$store.commit('LOCATION',{
      //       nodeName: data.city,
      //       nodeCode: '',
      //       allName: data.city + data.township
      //     })
      //   }
      // })
    })
  }
};
</script>
<style lang='scss'>
.hc-main-home {
  background-color: #f1f1f1;
  .hc-banner-bar {
    z-index: 500;
    background-color: rgba($color: #ffffff, $alpha: 0);
    position: fixed;
    color: #fff;
    height: 48px;
    width: 100%;
    left: 0px;
    top: 0px;
    -webkit-transform: translateZ(0);
    background-attachment:fixed;
    padding: 9px 15px;
    .hc-banner-loc {
      text-align: center;
      line-height: 30px;
      max-width: 140px;
      left: 0px;
      top: 0px;
      padding: 9px 15px;
      .icon {
        margin-left: 10px;
      }
    }
    .hc-banner-search {
      margin-left: 120px;
      input {
        height: 30px;
        padding-left: 30px;
        border-radius: 100px;
        color: inherit;
        background-color: rgba($color: #ccc, $alpha: 0.3);
      }
      // 表单占位符颜色
      input::-webkit-input-placeholder{
          color:#bfbfbf;
      }
      .hc-pa {
        top: 0px;
        left: 10px;
        line-height: 30px;
        color: #bfbfbf;
      }
    }
  }
  .hc-banner {
    background-color: #fff;
    height: px2rem(255);
    border-bottom: 1px solid #000;
    .mint-swipe-indicator {
      background-color: #ebebeb;
      opacity: 1;
      &.is-active {
        background-color: #f3ab03;
      }
    }

  }
  .hc-nav-content {
    background-color: #fff;
    height: 130px;
    .hc-nav-item {
      width: 20%;
      height: 100%;
      float: left;
      display: flex;
      align-items: center;
      color: #776e6f;
      img {
        font-size: 50px;
        margin-bottom: 10px;
        display: inline-block;
      }
    }
  }
  .hc-main-content {
    padding: 15px 15px 0px;
    .hc-main-item {
      background-color: #fff;
      width: 100%;
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.16);
      border-radius: 5px;
      padding: 15px 15px 15px 100px;
      margin-bottom: 10px;
      .hc-main-sign {
        width: 70px;
        height: 70px;
        border-radius: 50%;
        margin-right: 15px;
        left: 15px;
        top: 15px;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .hc-main-introduce {
        line-height: 20px;
        .hc-main-star {
          padding-top: 2px;
        }
        .hc-main-label {
          margin-top: 10px;
          color: #5ad28e;
          line-height: 18px;
          .hc-main-label-1 {
            border: 1px solid #5ad28e;
            display: inline-block;
            padding-left: 24px;
            padding-right: 6px;
            position: relative;
            margin-right: 20px;
            margin-bottom: 4px;
            .hc-main-label-icon {
              background-color: #5ad28e;
              display: inline-block;
              position: absolute;
              left: 0px;
              top: 0px;
              width: 18px;
              text-align: center;
              margin-right: 6px;
            }
          }
          .hc-label2-2 {
            padding-left: 6px !important;
            margin-right: 0px !important;
          }
        }
      }
    }
    .hc-loading {
      margin-bottom: 10px;
    }
    .mint-spinner-fading-circle {
      margin: auto;
    }
  }

}
</style>



